.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  height: var(--default-layout-header-height);
  background-color: var(--white-color);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12);
  z-index: 9999;
}

.container {
  width: var(--default-layout-width);
  padding: 0 var(--default-layout-horizontal-spacer);
  display: flex;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--white-color);
}

.logo-link {
  display: flex;
}

// Actions
.actions {
  display: flex;
  align-items: center;
}

.action-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 2.2rem;
  background-color: transparent;
  color: #161823;
  padding: 4px 12px;
  cursor: pointer;
}

.avatar {
  width: 32px;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  margin-left: 12px;
  cursor: pointer;
}

.menu-btn {
  font-size: 2rem;
  margin-left: 28px;
  padding: 4px 8px;
  background-color: transparent;
  cursor: pointer;
}

.badge {
  position: absolute;
  top: -10%;
  left: 24px;
  color: var(--white-color);
  font-weight: 600;
  font-size: 1.4rem;
  height: 20px;
  min-width: 20px;
  background-color: rgb(254, 44, 85);
  border-radius: 10px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
